        <v-col cols="12" class="buttons">
          <v-btn color="success" x-large elevation="24" @click="toggleVamos">¡Vamos!</v-btn>
          <VamosComponent v-show="showVamos"/>
        </v-col>
        <v-col cols="12" class="buttons">
          <v-btn color="error" x-large elevation="24" @click="toggleUpss">Upss</v-btn>
