      showVamos: false,
      showUpss: false
    }
  },
  components: {
    VamosComponent,
    UpssComponent,
    CuidadoComponent
  },
  methods: {
    toggleCuidado () {
      this.showCuidado = !this.showCuidado
    },
    toggleVamos () {
      this.showVamos = !this.showVamos
    },
    toggleUpss () {
      this.showUpss = !this.showUpss
    }
  },
  name: 'HomeView'

}
</script>

